import { Component, OnInit } from '@angular/core';
import { OnboardingService } from '../onboarding.service';
import {CellsComponentList, CoreMatTable, CoreMatTableInterface, displayedColumnsInterface, TableService} from "table";
import { Router, ActivatedRoute } from '@angular/router';
import { N } from '@angular/cdk/keycodes';

@Component({
  selector: 'lib-onboarding-list',
  templateUrl: './onboarding-list.component.html',
  styleUrls: ['./onboarding-list.component.scss']
})
export class OnboardingListComponent implements OnInit {
  onboardingList: any
  inputSearch = '';
  public search: string = '';
  public columnDefinitions: displayedColumnsInterface[] = [
    {
      key: 'Id',
      value: 'Id',
      order: 1,
      class: 'u-1',
      module: CellsComponentList.ButtonLink,
      sort: true,
      override: [`${this.service.mainPath}/request`, '_id']
    },
    {
        key: 'createdAt',
        value: 'Date de création',
        order: 2,
        class: 'u-2',
        module: CellsComponentList.DateFormat,
        sort: true
    },
    {
        key: 'ContactName',
        value: 'Contact',
        order: 3,
        class: 'u-2',
        sort: true,
        module: CellsComponentList.NameAvatar,
        override: ['ContactName', 'Email'],
    },
    {
      key: 'TemplateName',
      value: 'Template',
      order: 4,
      class: 'u-2'
    }
  ];
  localService: any;
  public _loading_table = true;
  private HiddenIndex = 0;
  public index = 0;
  lang = 'fr';

  cancelSearch() {
    this.inputSearch = '';
  }

  constructor(
    private service:OnboardingService,
    private router: Router,
    private route: ActivatedRoute,
  ) { }

  async ngOnInit() {
    setTimeout(async () => {
      await this.service.getAllOnboarding();
    }, 500);
    this.service.onboarding.subscribe((onboarding)=>{
      console.log(onboarding);
      setTimeout(() => {
        this._loading_table = false;
      }, 2000);

      if(onboarding){
        //this.onboardingList=onboarding;
        let x = onboarding.map((row, index) => {
          row.ContactName = row?.vcontacts[0]?.Name || '';
          row.Email = row?.vcontacts[0]?.Email || '';
          row.TemplateName = row.vtemplates.length > 0 ? row.vtemplates[0].Name : '';
          row.Id = index + 1;
          return row;
        })
        this.onboardingList = new CoreMatTable(x,
            {
              active: 'Id', direction: 'desc'
          }, {active: '', valueStart: null, valueEnd: null}, 15, true, true);
      }
    })
  }

  triggerChange($event) {
    if ($event.index !== this.HiddenIndex) {
        this._loading_table = true;


        this.index = $event.index;
        this.router.navigate(
            [],
            {
                relativeTo: this.route,
                queryParams: {page: null, tab: $event.index + 1},
                queryParamsHandling: 'merge', // remove to replace all query params by provided
            });
        this.HiddenIndex = $event.index
        setTimeout(() => {
            this._loading_table = false;
        }, 2000);
    }
}

}
