import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { ENTER, COMMA, SEMICOLON } from '@angular/cdk/keycodes';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'lib-sub-create-onboarding',
  templateUrl: './sub-onboarding-create.component.html',
  styleUrls: ['./sub-onboarding-create.component.scss']
})
export class SubOnboardingCreateComponent implements OnInit {
  @Input() field: any
  @Input() length: any
  @Input() i: any
  @Input() j: any
  @Output() onAddKeywordFromInput = new EventEmitter<any>()
  @Output() onKeyPress = new EventEmitter<any>()
  @Output() onDrop = new EventEmitter<any>()
  @Output() onAddNewField = new EventEmitter<any>()
  @Output() onRemoveField = new EventEmitter<any>()
  @Output() onChangeType = new EventEmitter<any>()
  @Output() onRemoveKeyword = new EventEmitter<any>()
  @Output() onAddNewSubLevelField = new EventEmitter<any>()
  @Output() onExpendMore = new EventEmitter<any>()
  @Output() onChangeLabel = new EventEmitter<any>()
  @Output() onCheckedChip = new EventEmitter<any>()

  chipControl = new FormControl(new Set());

  separatorKeysCodes = [ENTER, COMMA, SEMICOLON];
  currentChip=""
  prevValue=""
  constructor() { }

  ngOnInit(): void {
  }

  drop(event) {
    this.onDrop.emit(event)
  }

  addKeywordFromInput(event, i, j) {
    this.onAddKeywordFromInput.emit({ event,prev:this.prevValue, i, j });
  }

  keyUp(event, i, j) {
    this.onKeyPress.emit({ event, i, j })
  }

  addNewField(event, i) {
    event.preventDefault()
    this.onAddNewField.emit({ event, i })
  }

  removeField(event, i, j) {
    event.preventDefault()
    this.onRemoveField.emit({ i, j })
  }

  changeType(type, i, j) {
    this.onChangeType.emit({ type, i, j })
  }

  removeKeyword(event, i, j) {
    this.onRemoveKeyword.emit({ event, i, j })
  }
  addNewSubLevelField(e) {
    e.preventDefault()
    this.onAddNewSubLevelField.emit(true)
  }
  expendMore(e, field) {
    e.preventDefault()
    this.onExpendMore.emit({expand:true, field})
  }

  ingoreDefaultAction(e) {
    e.preventDefault()
  }

  changeLabel(label,key){
    this.onChangeLabel.emit({label, key})
  }

  toggleChip = (chip: any) => {
    this.onCheckedChip.emit(chip)
    // const addChip = () => { this.chips.add(chip); };
    // const removeChip = () => { this.chips.delete(chip); };
    // this.chips.has(chip) ? removeChip() : addChip();

  }

  onEditChip(chip){
    if(chip.value===this.currentChip){
      this.currentChip=""
      this.prevValue=""
    }else{
      this.currentChip=chip.value;
      this.prevValue=chip.value
    }
  }

  get chips() { return this.chipControl.value; }

  formatOther(other:string){
   if(other){
    return other.toLocaleLowerCase()
   }
   return ''
  }


  checkSubForm(forms){
    let hasSubForm=false;
    if(forms.forms && forms.forms.length){
      hasSubForm=true;
    }    
    return hasSubForm
  }

}
